<template><div class="content"><h1 class="post-title">过去的一切<a class="header-anchor" id="%E8%BF%87%E5%8E%BB%E7%9A%84%E4%B8%80%E5%88%87" href="#%E8%BF%87%E5%8E%BB%E7%9A%84%E4%B8%80%E5%88%87">#</a></h1><div class="metabar"><div class="metabar-item">2021/11/27</div><div class="metabar-item">思想</div><div class="metabar-item">约 18 分钟读完</div></div>
<p>不知许久，我失去了对自我情绪的掌控。也许是发自对自己周边环境的厌恶罢，总是感到百无聊赖，做着让自己不能获得任何成就感却又颇有着「前途」的事情。这似乎是我一直以来的心结，是我自熟悉这个世界以后就存在的心结。怎么说呢？是不是因为现实世界的浮躁与对视听的混淆，以至于我渐渐淡忘了对于解开它的强烈需求，直到最后它把我压到喘不过气来。</p>
<p>我一直不想要去承认自己比别人独特，比别人高大，这也不是什么三言两语能说的清楚的事情。我也许会说</p>
<blockquote>
<p>我讨厌这个世界上对个人的价值评定标准：在宏观上，这个人有钱、有权，他就是优越的；放微观来说，这个人踏实、奋斗且有所成就，他就是优越的；等等。</p>
<p>然而<strong>某个个体</strong>是否优秀，对于<strong>我</strong>来说重要吗？<strong>一个人</strong>做了什么伟大的事情，就能说明他对<strong>我</strong>有价值吗？这个<strong>社会</strong>进步了，就能代表<strong>我</strong>进步了吗？</p>
</blockquote>
<p>上面的话，很多人可能看不惯，但这就是我的真实想法。诚然，它不能改变什么，甚至是一种愤世嫉俗的典型表现，但我认为它反映了一定意义上自我的本质。人是群居动物，但<strong>绝不能</strong>作为人不能孤独的论据。</p>
<p>该怎样去定义自私呢？我不懂。但我一直想要为自己的利益而活，而不是为他人的利益而活，同时我不认为我是自私的。说这句话，不代表着我看到了他人受难就不予以关注，因为「怜悯乃是人类的天性」；不代表我不会参加任何公益事业，因为我不是在排斥奉献；不代表我会损害别人的利益来达到自己的利益，因为我是善良的。但是，在生活轨迹上，我却会将其作为生活的主动轮。这样有什么错吗？</p>
<p>我不敢说我这种情况不是麻木不仁的，毕竟我在看到非我所在的或所偏爱的任何集体得到广泛关注、取得成功以后并不会发自内心地感到高兴。也许我这样的人放到旧中国里很危险，在现在的舆论场上，也就是我认为我国所特有的长期充斥着一种好比在国家蒙受巨大且真实的苦难之后的积极对抗、积极发展的心理的大众圈里，也是被批驳的对象，但我认为在十分严格（代表着很精准）的逻辑框架下，我的思维逻辑是没有本质性的错误的。</p>
<p>对事物的见解，我不想因为许多人认为它是对的而认为它就是对的；不想因为它顺从大多数人的价值取向就认为它是正当的、高尚的，是值得我的崇拜的；不想因为大多数都会走这条路就去走这条路，并且否定其它的道路；更不愿意相信由某些教条式的因素或者不明不白的逻辑所得出的 <em>supposed</em>。总之我渴望自由，我渴望在有些事情上能由自己做决定、说了算——而这决定也<strong>绝不是</strong>某些人嘴边常挂的所谓「你所奋斗而来的权力」，而是我生而为人本所能掌控，却被这个社会的浮躁所蒙蔽、排斥的权利。</p>
<p>焦虑让我伤感，让我感到无比迷茫。让我即使在少有的能够欣赏这个世界上我所喜爱的部分的时候，也能在一瞬间降落到「我什么时候死」、「我会不会猝死」、「癌症潜伏期多久」一类在外界人看来十分夸张和病态的低谷里。对于那些我日常所处的环境中，更是会有干扰我的思绪，例如「我说的话对他有什么样的影响，对我有什么样的影响？」，<em>后来我发现这其实是一种另类的自恋</em>。焦虑让我担心我现在的所有奋斗是否会在将来的某一刻彻底落空，让自己变成完全没有价值的人。</p>
<p>这显然是病态的，显然不是正常人所能想出来的东西。在过去的一段时间里，我也因此而饱受烦恼。直到后来我的调整，我才发现，这些担忧的对象其实都是真实存在的（没有人会去担忧不存在或者公认不可能发生的事情），焦虑只是让这一切放大化了。对于明天是否会死这个问题，正常人所想的应该是：寿命还没到那一节，除非被车撞，过马路小心一点就行。哪怕真的死了，也只是一瞬间的事情。而焦虑的人所想的却是：明天被车撞也不是没有可能，自己得癌症也不是没有可能，明天死也不是没有可能——对于「不是没有可能」感到异常的敏感，对事物的思考过于理想化，总是想要让事物能够对外呈现一个绝对化的结果，只要不绝对就会感到不安，是这种思维模式的本质特征。</p>
<p>而大多数风凉人所常唠叨的那几句不痛不痒的所谓安慰：「想这么多干什么？这么矫情干什么？你这样会很累的不知道吗？」这与大多数对心理疾病患者所抱有偏见的人的典型表现如出一辙。如果这是你，希望你能够认清这些话语对于长期处于这样心理状态下的人实际上是一种残忍。</p>
<p>进行了 <a href="https://www.idrlabs.com/cn/dark-core-personality/test.php" class="external-link" target="_blank" rel="noopener noreferrer">D 测试<span class="external-link-icon mdi mdi-launch"></span></a>以后，我发现自己在自恋方面的得分比其它方面要高出很多。在查找了相关资料（自恋的表现等，包括维基百科、百度百科和知乎上相关我所认为还算正规的文章）以后发现，自己也挺符合<sup>注1</sup>。</p>
<p><img src="https://z3.ax1x.com/2021/11/27/oeLeKg.jpg" alt=""></p>
<p>我属于那种不太会在外界刻意表现自己的自恋者，因为也许我先前受到的这方面的负面反馈比较多。我坚信如果我这样会非常不受欢迎，同时我也认可不应该这样做，而应该踏实稳妥，遇到不好的笑笑，遇到好的闭嘴惊艳即可。自恋是自卑的一种表现，因为我长期以来都是沉浸于自己的世界里，导致与现实世界中的人的共同语言很少，甚至思想不处于同一个层级（在这里，我不说是高了还是低了，我也不敢评判；但总之，并不是一个层级的）。孤独的恶性循环，先开始我认为这样很好，但后来不知是因为周围人浮躁<sup>注2</sup>的影响，还是个人的本性作祟，越来越觉得失落。</p>
<p>既然外界并没有太多对我的负面评价，我也很克制自己输出负面情绪（我在外界的表现其实还是很乐观的），我究竟在追求什么呢？这个问题一下子问倒了我。也许就是自恋吧：总是觉得别人做的事情（在我能力范围内的）换自己来做会更好；总是觉得自己在别人心中处于一个偏向于焦点的地位以至于在意别人对自己的评价——但也许自己只是别人心中的路人而已；害怕被看淡，被忽视。我很胆怯，在某种意义上来说是一个好事。我不会去主动这样表达，而往往是自己内部消化。因而，这带来的至少只是我自身的不好感受，而非外界的形象折损。</p>
<p>有的时候，我告诉自己这样想没什么问题。譬如，他们写文章，都会写成这个样子：</p>
