<p>这也许也是一种强迫症？</p>
<p>可以说，我的情绪一直是波动的。就好比是朴树的专辑《我去 2000 年》里的两首歌，一首《妈妈，我...》里的愤世嫉俗，</p>
<blockquote>
<p>我恶心，在他们的世界，生活是这么旧，让我总不快乐。我活的不耐烦，但是又不想死，他们是真这么硬，就让我撞它，撞得头破血流吧。</p>
</blockquote>
<p>以及《在希望的田野上》中的宽慰，</p>
<blockquote>
<p>你的生命它不长，不能用它来悲伤。让该来的来，我们在这里等待。我们就这样唱······</p>
</blockquote>
<p>我发觉了自己的矛盾：哪怕是在要贬低一个人的时候，似乎存在的另一个人格也在告诉我没有必要，但我的愤怒却又是真真切切发生了的。等等。</p>
<p>总之我想要改变，想要摆脱这么久以来自己的纠结。不说多了，就从今天开始吧。</p>
<h2>注释<a class="header-anchor" id="%E6%B3%A8%E9%87%8A" href="#%E6%B3%A8%E9%87%8A">#</a></h2>
<h3>注 1</h3>
<blockquote>
<p>进行了 <mark>D 测试</mark>以后，我发现自己在自恋方面的得分比其它方面要高出很多。在查找了相关资料（自恋的表现等，包括维基百科、百度百科和知乎上相关我所认为还算正规的文章）以后发现，<mark>自己也挺符合</mark>。</p>
</blockquote>
<ol>
<li>我对于心理、人格测试的观点，其实还算比较中立。一些乱七八糟的测试我不会做。这个测试的设计有现存 PhD 的参与，我还是随性参与了，并且认为它的结果具有一定的参考价值。</li>
<li>我没有那么喜欢给自己贴标签，即没有那么喜欢去刻意搜索一些心理疾病、心境的症状然后往自己身上靠从而获得一种身份的认同。我对自己是否自恋的认定是经过了对资料的参考和深思熟虑的。</li>
</ol>
<h3>注 2</h3>
<blockquote>
<p>···孤独的恶性循环，先开始我认为这样很好，但后来不知是因为<mark>周围人浮躁</mark>的影响，还是个人的本性作祟，越来越觉得失落。</p>
</blockquote>
<p>不是因为我很喜欢评价他人，而是他们的表现确实过火。例如对于一个很厉害的人所做出的厉害的功绩，他们并没有思考自己的实际能力、情况和生活目标，而是一味的吹捧并传播，我不是很能理解这样做的意义。例如他们对价值的认定过于单一，让我很烦恼，很难从现实生活中获得乐趣，而只能或自给自足，或在网上找事做、找人聊。这些想法也都只产生于心中，从来没有直接或间接地表达给现实中的任何人。</p>
